import { useEffect } from 'react';
import Layout from '../../../components/Layout';
import Sidebar from './../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { selectCategories, getCategories, selectGettingCategoryData } from '../../../redux/pcategoryReducer';
import LoadingData from '../../../components/LoadingData';

const POS = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const categories = useSelector(selectCategories);
  const loading = useSelector(selectGettingCategoryData);

  const handleDetails = (cate) => {
    navigate(`/details/${cate}`);
  }

  useEffect(() => {
    dispatch(getCategories());
  }, [])

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Point of Sale</p>
              </div>
              {/* { loading ? <LoadingData /> : */}
              <div className="grid grid-cols-3 gap-6 px-[2rem] py-[2rem]">
                  {
                      categories.map((cate, index) => {
                          return (
                              <div key={index} onClick={() => handleDetails(cate._id)} className='w-full h-[6rem] text-xl text-center leading-[6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'>
                                  {cate.name}
                              </div>
                          )
                      })
                  }
              </div>
              {/* } */}
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default POS;